				]
			};
		},
		created:function () {
			//this.getContents();
		},
		computed: {
			//计算函数
			listTemp: function () { //数据分组
				var list = this.list;
				var arrTemp = [];
				var index = 0;
				var sectionCount = 3; //一行显示多少个
				for (var i = 0; i < list.length; i++) {
					index = parseInt(i / sectionCount); //取余数
					if (arrTemp.length <= index) { //每隔 sectionCount 个数据添加一行
						arrTemp.push([]);
					}
					arrTemp[index].push(list[i]); //每行添加 sectionCount 个数据
				}
				return arrTemp;
			}
		},
		methods: {
			getContents() {//获取目录面板数据
				var self = this;
				uni.showLoading({
					title: '正在获取'
				});
				const result = service.getContents(1, 1, function() {
// 					uni.showToast({
// 						icon: 'none',
// 						title: '登录成功'
// 					});
				});
			},
			tabonclick(_name,_path) {//点击跳转
			console.log(_path);
				var self = this;
				if(_name=="我的资料")
				{
					uni.reLaunch({
						url: '../user/user'
					});
				}else{
					uni.navigateTo({
						url: _path
					});
				}


			},
		}
	};
